@import '../../styles/variables.scss' ;

.leftPanelGroupItem {
  width: 100%;
  margin-bottom: 20px;
}


.groupTitle {
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
}


.iconMenu{
  min-width: 35px;
}

.tabHeader {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    border-bottom: 1px solid;
  }
}
